import React from 'react';
import { Input} from 'antd';
var registerCss = require('./register.css');
export default class Register extends React.Component {
    render() {
        return (
            <div className={registerCss.H}>
                <div className={registerCss.h1}>
                    <div className={registerCss.h2}>
                        <span></span>
                    </div>
                    <from action="#" method="post">
                        <div className={registerCss.h3}>
                            <span className={registerCss.b}>*</span>
                            <label for="username"></label>
                            <div className={registerCss.h4}>
                                <input type="text" className={registerCss.h5} name="username" placeholder=" :6-12" />
                            </div>
                        </div>
                        <div className={registerCss.a1}>
                            <span className={registerCss.b}>*</span>
                            <label for="userPassword"></label>
                            <div className={registerCss.a2}>
                                <Input.Password type="password" className={registerCss.aa} name="userPassword" placeholder=" :6-12" />
                            </div>
                        </div>
                        <div className={registerCss.a3}>
                            <span className={registerCss.b}>*</span>
                            <label for="userRePassword"></label>
                            <div className={registerCss.a4}>
                                <Input.Password type="password" className={registerCss.a5} name="userRePassword" placeholder=""  />
                            </div>
                        </div>
                        <div className={registerCss.c1}>
                            <span className={registerCss.b}>*</span>
                            <label for="userPhone"></label>
                            <div className={registerCss.c2}>
                                <input type="text" className={registerCss.c3} name="userPhone" placeholder="11" />
                            </div>
                        </div>

                        <div className={registerCss.d1}>
                            <span className={registerCss.b}>*</span>
                            <label for="userEmail"></label>
                            <div className={registerCss.d2}>
                                <input type="text" className={registerCss.d3} name="userEmail" placeholder="123@qq.com" />
                            </div>
                        </div>
                        <div className={registerCss.e1}>
                            <span className={registerCss.b}>*</span>
                            <label for="userGender"></label>
                            <div className={registerCss.e2}>
                                <input type="radio" id="userGender_01" name="userGender" value="0" checked="checked" />
						<input type="radio" id="userGender_02" name="userGender" value="1" />
					</div>
                        </div>
                        <div className={registerCss.f}>
                            <input className={registerCss.f1} type="submit" value=""></input>
                        </div>
                        <div className={registerCss.g}>
                            <a href="./../denlu" className={registerCss.g5}></a>
                        </div>
                            <a href="./../" className={registerCss.g6}></a>
                    </from>
                </div>
            </div>
        )
    }
}
